/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.6rem;
}

p {
  color: hsl(232deg, 10%, 55%);
  font-size: 1.8rem;
  line-height: 1.5;
}

.contenedor {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Fraunces", serif;
  font-weight: 900;
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  background-image: url(../img/mobile/image-header.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 55rem;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    background-image: url(../img/desktop/image-header.jpg);
  }
}
.header .header-content .header-bar {
  position: absolute;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .header .header-content .header-bar .logo-menu {
    padding: 2rem;
    display: none;
  }
}
.header .header-content .header-bar .links {
  display: none;
}
.header .header-content .header-bar .links a {
  color: hsl(0deg, 0%, 100%);
  padding: 1rem 2rem;
}
.header .header-content .header-bar .links :last-child {
  color: hsl(212deg, 27%, 19%);
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  font-size: 1.5rem;
  background-color: hsl(0deg, 0%, 100%);
  border: 0.1rem solid hsl(0deg, 0%, 100%);
  border-radius: 3rem;
}
.header .header-content .header-bar .links :last-child:hover {
  color: hsl(0deg, 0%, 100%);
  background-color: hsl(199deg, 100%, 75%);
  border: 0.1rem solid hsl(199deg, 100%, 75%);
}
@media (min-width: 1024px) {
  .header .header-content .header-bar .links {
    display: flex;
    justify-content: space-around;
    width: 40%;
  }
}
.header .header-content .header-copy,
.header .header-content .arrow-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 0;
  right: 0;
}
.header .header-content .header-copy {
  top: 0;
  bottom: 30%;
}
@media (min-width: 768px) {
  .header .header-content .header-copy {
    top: 0;
    bottom: 50%;
  }
}
.header .header-content .header-copy p {
  color: hsl(0deg, 0%, 100%);
  font-family: "Fraunces", serif;
  font-size: 4rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 900;
  letter-spacing: 0.5rem;
  line-height: 1.2;
}
.header .header-content .arrow-logo {
  bottom: 0;
  top: 30%;
}
@media (min-width: 768px) {
  .header .header-content .arrow-logo {
    bottom: 0;
    top: 5%;
  }
}

.learn-more .about {
  height: 100vh;
  background-repeat: no-repeat;
  background-position: center top;
  display: grid;
  background-size: 60vh;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .learn-more .about {
    height: 60rem;
    background-size: contain;
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: none;
  }
}
.learn-more .about .text {
  grid-row: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 3rem 2rem;
}
@media (min-width: 1024px) {
  .learn-more .about .text {
    padding: 15rem;
    justify-content: space-around;
    align-items: flex-start;
  }
}
.learn-more .about .text h1, .learn-more .about .text p, .learn-more .about .text a {
  text-align: center;
}
@media (min-width: 1024px) {
  .learn-more .about .text h1, .learn-more .about .text p, .learn-more .about .text a {
    text-align: left;
  }
}
.learn-more .about .text h1 {
  color: hsl(212deg, 27%, 19%);
}
.learn-more .about .text a {
  text-transform: uppercase;
  font-family: "Fraunces", serif;
  color: hsl(212deg, 27%, 19%);
  position: relative;
  z-index: 10;
}
.learn-more .about .text a::after {
  content: "";
  z-index: -1;
  width: 100%;
  height: 0.5rem;
  position: absolute;
  left: 0;
  top: 70%;
  border-radius: 1rem;
  transition: 0.15s;
  opacity: 0.3;
}
.learn-more .about .text .yellow-anchor::after {
  background-color: hsl(51deg, 100%, 49%);
}
.learn-more .about .text .pink-anchor::after {
  background-color: hsl(7deg, 99%, 70%);
}
.learn-more .brand {
  background-image: linear-gradient(to bottom, transparent 50%, hsl(0deg, 0%, 100%) 0%), url(../img/mobile/image-transform.jpg);
}
@media (min-width: 768px) {
  .learn-more .brand {
    background-image: linear-gradient(to left, transparent 50%, hsl(0deg, 0%, 100%) 0%), url(../img/desktop/image-transform.jpg);
    background-position: center right;
  }
  .learn-more .brand .text {
    grid-column: 1/2;
    grid-row: 1;
  }
}
.learn-more .audience {
  background-image: linear-gradient(to bottom, transparent 50%, hsl(0deg, 0%, 100%) 0%), url(../img/mobile/image-stand-out.jpg);
}
@media (min-width: 768px) {
  .learn-more .audience {
    background-image: linear-gradient(to right, transparent 50%, hsl(0deg, 0%, 100%) 0%), url(../img/desktop/image-stand-out.jpg);
    background-position: center left;
  }
  .learn-more .audience .text {
    grid-column: 2/3;
    grid-row: 1;
  }
}

.features .features-content {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .features .features-content {
    flex-direction: row;
  }
}
.features .features-content .feature {
  background-repeat: no-repeat;
  background-position: center top;
  background-size: contain;
  height: 60rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .features .features-content .feature {
    background-size: cover;
    width: 50%;
  }
}
.features .features-content .feature .text {
  padding: 3rem 2rem;
  grid-row: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}
@media (min-width: 1024px) {
  .features .features-content .feature .text {
    padding: 8rem 15rem;
  }
}
.features .features-content .feature .text h1,
.features .features-content .feature .text p {
  text-align: center;
}
.features .features-content .feature .text h1 {
  font-size: 3rem;
}
.features .features-content .design {
  background-image: linear-gradient(to bottom, transparent 50%, transparent 0%), url(../img/mobile/image-graphic-design.jpg);
}
@media (min-width: 768px) {
  .features .features-content .design {
    background-image: linear-gradient(to bottom, transparent 50%, transparent 0%), url(../img/desktop/image-graphic-design.jpg);
  }
}
.features .features-content .design .text h1, .features .features-content .design .text p {
  color: hsl(167deg, 40%, 24%);
}
.features .features-content .photography {
  background-image: linear-gradient(to bottom, transparent 50%, transparent 0%), url(../img/mobile/image-photography.jpg);
}
@media (min-width: 768px) {
  .features .features-content .photography {
    background-image: linear-gradient(to bottom, transparent 50%, transparent 0%), url(../img/desktop/image-photography.jpg);
  }
}
.features .features-content .photography .text h1, .features .features-content .photography .text p {
  color: hsl(198deg, 62%, 26%);
}

.testimonials {
  background-color: hsl(0deg, 0%, 98%);
  padding: 5rem 2rem;
}
@media (min-width: 1024px) {
  .testimonials {
    padding: 10rem 2rem;
  }
}
.testimonials .heading {
  text-align: center;
  font-size: 1.6rem;
  margin: 2rem 0;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
  color: hsl(210deg, 4%, 67%);
}
@media (min-width: 1024px) {
  .testimonials .testimonials-content {
    display: flex;
    padding: 0 5rem;
    justify-content: space-around;
  }
}
.testimonials .testimonials-content .testimonial {
  padding: 4rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
@media (min-width: 1024px) {
  .testimonials .testimonials-content .testimonial {
    padding: 4rem 2rem;
  }
}
.testimonials .testimonials-content .testimonial .avatar {
  width: 25%;
  padding: 1rem 0;
}
.testimonials .testimonials-content .testimonial .avatar img {
  margin: 0 auto;
  border-radius: 50%;
}
.testimonials .testimonials-content .testimonial p, .testimonials .testimonials-content .testimonial h2, .testimonials .testimonials-content .testimonial span {
  text-align: center;
}
.testimonials .testimonials-content .testimonial h2 {
  padding: 2rem;
  font-size: 2rem;
}
.testimonials .testimonials-content .testimonial .position {
  color: hsl(210deg, 4%, 67%);
}

.tiles {
  display: grid;
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 1024px) {
  .tiles {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(4, 1fr);
  }
}

.footer {
  background-color: hsl(168deg, 57%, 72%);
}
.footer .footer-content {
  padding: 6.4rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .footer .footer-content {
    padding: 4rem 0;
  }
}
.footer .footer-content .logo, .footer .footer-content .links, .footer .footer-content .socials {
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .footer .footer-content .socials {
    padding: 3rem 0 0 0;
  }
}
.footer .footer-content .logo {
  cursor: pointer;
}
.footer .footer-content .logo img {
  width: 18rem;
}
@media (min-width: 1024px) {
  .footer .footer-content .logo img {
    width: 14rem;
  }
}
.footer .footer-content .links {
  display: flex;
  justify-content: space-around;
  width: 90%;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .footer .footer-content .links {
    justify-content: space-around;
    width: 25%;
  }
}
.footer .footer-content .links a {
  font-size: 1.8rem;
  color: hsl(167deg, 40%, 24%);
}
.footer .footer-content .links a:hover {
  color: hsl(0deg, 0%, 100%);
}
.footer .footer-content .socials {
  display: flex;
  justify-content: space-around;
  width: 60%;
}
@media (min-width: 1024px) {
  .footer .footer-content .socials {
    justify-content: space-around;
    width: 20%;
  }
}
.footer .footer-content .socials .social i {
  color: hsl(167deg, 40%, 24%);
  font-size: 2.5rem;
  cursor: pointer;
}
.footer .footer-content .socials .social i:hover {
  color: hsl(0deg, 0%, 100%);
}